body {
  color: #313D48;
  font-family: "Ubuntu", Arial, sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.44;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
}

.btn {
  position: relative;
  padding: 9px 14px;
  color: #fff;
  font-family: "Ubuntu", Arial, sans-serif;
  font-size: 13px;
  font-weight: 300;
  letter-spacing: 0.3em;
  line-height: 1.25;
  text-transform: uppercase;
  border: 1px solid #5FA9F9;
  border-radius: 30px;
  vertical-align: top;
  overflow: hidden;
  background-color: transparent;
  box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.36);
  display: inline-block;
}

.no-touch .btn:hover:after {
  left: 120%;
  transition: left 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.btn:after {
  position: absolute;
  content: "";
  left: -75px;
  top: -50px;
  width: 50px;
  height: 155px;
  opacity: .2;
  transform: rotate(35deg);
  transition: left 550ms cubic-bezier(0.19, 1, 0.22, 1);
  background-color: #5FA9F9;
}

.btn_lg {
  padding: 15px 47px;
  border-width: 2px;
  box-shadow: 0 3px 15px 0 transparent;
}

@media only screen and (max-width: 767px) {
  .btn_lg {
    padding: 15px 27px;
  }
}

.btn_small {
  padding: 7px 10px;
  font-size: 10px;
  letter-spacing: 0.25em;
  box-shadow: 0 5px 18px 0 rgba(0, 0, 0, 0.33);
}

.btn_blue {
  color: #5FA9F9;
}

.title-lg {
  font-family: "Ubuntu", Arial, sans-serif;
  font-weight: 300;
  font-size: 35px;
  line-height: 1;
  letter-spacing: -0.002em;
}

.title-md {
  font-family: "Ubuntu", Arial, sans-serif;
  font-weight: 300;
  font-size: 30px;
  line-height: 1.2;
  letter-spacing: -0.002em;
}

@media only screen and (max-width: 767px) {
