.form {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s, visibility .3s;
  padding: 55px 0 57px;
  color: #fff;
  background: #2C3944;
}

.form.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.form__inner {
  position: relative;
}

.form__close {
  position: absolute;
